import styles from "./SliderPannel.module.css";
import icon1 from "../assets/image_16.png";
import icon2 from "../assets/image_14.png";
import icon3 from "../assets/image_15.png";
import icon4 from "../assets/image_17.png";
import icon5 from "../assets/image_18.png";
import icon6 from "../assets/image_19.png";
import BetAmount from "./BetAmount.js"
import classnames from "classnames";
import { useSelector } from "react-redux";
import { getPriceFromOracle, getRoundData,isClaimable,getUserBet,claim } from "../utils/contractData.js";
import { useEffect, useState } from "react";
import reactDom from "react-dom";




function SliderPannel(props, state) {


  const currenctCurrency = useSelector(state => state.currentCurrency);


  var iconList = [icon1, icon2, icon3, icon4, icon5, icon6];
  var currentIcon = iconList[currenctCurrency];
  var mycss;
  if (props.isActive == true)
    mycss = styles.nozoom;
  else
    mycss = styles.zoomin;

  if (props.status == "next") {
    return (<NextSlide mycss={mycss} currentIcon={currentIcon} />);
  }
  else if (props.status == "expired1") {
    return (<ExpiredSlide1 mycss={mycss} currentIcon={currentIcon} />);
  }
  else if (props.status == "expired2") {
    return (<ExpiredSlide2 mycss={mycss} currentIcon={currentIcon} />);
  }
  else if (props.status == "live") {
    return (<LiveSlide mycss={mycss} currentIcon={currentIcon} />);

  }
  else if (props.status == "future1") {
    return (<FutureSlide1 mycss={mycss} currentIcon={currentIcon} />);
  }
  else {
    return (<FutureSlide2 mycss={mycss} currentIcon={currentIcon} />);
  }


}

function LiveSlide(props) {

  const calculated=useSelector(state=>state.calculated)
  const paused=useSelector(state=>state.paused);
  var pausedCss3=styles.hide;var pausedCss1=null; var pausedCss2=null;
  if(calculated==false&&paused==false)
  {
    pausedCss3=null;
    pausedCss1=styles.hide;
    pausedCss2=styles.hide;

  }
  else  if(calculated==true&&paused==false)
  {
    pausedCss3=styles.hide;
    pausedCss1=null;
    pausedCss2=styles.hide;
  }
  else  if(calculated==false&&paused==true)
  {
    pausedCss3=styles.hide;
    pausedCss1=styles.hide;
    pausedCss2=null;
  }
  else  if(calculated==true&&paused==true)
  {
    pausedCss3=styles.hide;
    pausedCss1=styles.hide;
    pausedCss2=null;
  }


  
  const [roundData, setRoundData] = useState(null);
  //const currentCurrency=useSelector(state=>state.currentCurrency)
  const [lastPrice,setLastPrice]=useState(null);
  var buttonCss=styles.button2;
  var different=null;
  var locked=null;
  var last=null;
  if(roundData!=null)
  {
  locked=(Number(roundData.lockPrice).toFixed(3));
  if(lastPrice!=null)
  {
different=lastPrice-roundData.lockPrice;
different=(Number(different).toFixed(3));
last=(Number(lastPrice).toFixed(3));
}
  
  }
  if(different<0)
  buttonCss=styles.button22;

  const num = useSelector(state => state.num);
  var mycss = props.mycss;
  var currentIcon = props.currentIcon
  useEffect(() => {
    async function f() {
    
      if (num > 1) {
        var r = await getRoundData(num - 1)
  
        setRoundData(r);
      }

    }
    f();
  },[num]);
  useEffect(()=>{
  
    const interval=setInterval(function(){
      getPriceFromOracle().then((price)=>{
setLastPrice(price);
      })
      }, 3000);
     

     return () => clearInterval(interval);

  },[])


  return (

    <div>

      <div className={styles.center, mycss}>
        <div className={styles.box1}>
          <div className={classnames(styles.row1)}>
            <div className={classnames(styles.col1, "font_24", "font_5_s", "font_26_m")}>
              <div className={styles.live}>Live</div>
            </div>
            <div className={classnames(styles.col2, "font_5", "font_5_s", "font_26_m")}>
              <div className={styles.three}>#{parseInt(num) - 1}</div>
            </div>
            <div className={styles.col3}>
              <img src={currentIcon} className={styles.icon1}></img>
            </div>
          </div>
          <hr className={styles.hr}></hr>
          <div className={styles.row2}>
            <div className={classnames(styles.col1, "font_24", "font_5_s", "font_26_m")}>
              <div className={styles.lock}>Locked Price</div>
            </div>
            <div className={classnames(styles.col2, "font_21", "font_4_s", "font_27_m")}>
              <div className={styles.num}>{locked}</div>
            </div>
          </div>

          <div className={styles.box3}>
            <div className={classnames(styles.up, "font_5", "font_23_s", "font_17_m")}>UP</div>
            <div className={classnames(styles.x2, "font_21", "font_4_s", "font_27_m")}>X2,1</div>
          </div>
          <div className={classnames(styles.box22,pausedCss3)}>calculating</div>
          <div className={classnames(styles.box22,pausedCss2)}>Prediction has been paused</div>
          <div className={classnames(styles.box2,pausedCss1)}>
            
            <div className={styles.row11}>
              <div className={styles.col11}>
                <div className={classnames(styles.last, "font_24", "font_23_s", "font_26_m")}>
                  Last Price
                </div>
                <div className={classnames(styles.num2, "font_21", "font_4_s", "font_27_m")}>
                  {last}
                </div>
                <div className={classnames(styles.prize, "font_24", "font_23_s", "font_26_m")}>
                  Prize Pool:
                </div>
              </div>
              <div className={styles.col21}>
                <div className={classnames(styles.btn4, "font_5", "font_23_s")}><button className={classnames(buttonCss, "font_5", "font_23_s", "font_26_m")}>{different}</button></div>
                <div className={classnames(styles.num3, "font_21", "font_4_s", "font_27_m")}>{roundData?.totalAmount}</div>
              </div>
            </div>

          </div>
          <div className={styles.box4}>
            <div className={classnames(styles.x1, "font_21", "font_4_s", "font_27_m")}>X1,5</div>
            <div className={classnames(styles.down, "font_5", "font_23_s", "font_17_m")}>Down</div>
          </div>

          <Progress />
        </div>
      </div>

    </div>
  );

}
