import oracleAddresses from "./constants/oracles.json";
import tokenContracts from "./constants/tokenContracts.json";
import logEvents from "./constants/logEvents.json";
import BigNumber from "bignumber.js";
const BIG_TEN = new BigNumber(10);

const bscURL = "https://bsc-dataseed.binance.org/";
const bscTestNetURL = "https://data-seed-prebsc-1-s1.binance.org:8545";
