let predictionIndex;
let predictionAddress;
let pauseEventSubscription;
let predictionContract;
let bettingTokenContract;
let oracleAddress;

const predictionContracts = {};
const predictionAddresses = {};
const bettingTokenAddresses = {};

const getPredictionContract = async (_predictionIndex) => {
    if(predictionContracts[_predictionIndex])
        return predictionContracts[_predictionIndex];
    
    const address = await factoryContract.methods.predictions(_predictionIndex).call();
    const contract = new web3.eth.Contract(predictionABI, address);
    predictionAddresses[_predictionIndex] = address;
    predictionContracts[_predictionIndex] = contract;

    return contract;
}

export const setPredictionIndex = async (_predictionIndex) => {
    predictionIndex = _predictionIndex;
    predictionContract = await getPredictionContract(_predictionIndex);
    predictionAddress = predictionAddresses[_predictionIndex];
    oracleAddress = await getOracle(_predictionIndex);
}

export const getPredictionCount = async () => {
    const result = await factoryContract.methods.predictionCount().call();
    return result;
}

export const getPredictionTargetToken = async (_predictionIndex = null) => {
    const pid = _predictionIndex ? _predictionIndex : predictionIndex;
    const oracleAddr = await getOracle(pid);
    return oracleAddresses[oracleAddr].tokenName;
}

export const getPredictionBettingTokenName = async (_predictionIndex = null) => {
    const tokenAddr = await getPredictionBettingTokenAddress(_predictionIndex);
    return tokenContracts[tokenAddr].symbol;
}

export const getPredictionBettingTokenAddress = async (_predictionIndex = null) => {
    const pid = _predictionIndex ? _predictionIndex : predictionIndex;
    
    if(bettingTokenAddresses[_predictionIndex])
        return bettingTokenAddresses[_predictionIndex];

    const tokenAddr = await factoryContract.methods.betTokens(pid).call();
    bettingTokenAddresses[_predictionIndex] = tokenAddr;
    return tokenAddr;
}

export const getPredictionIntervalSeconds = async () => {
    const interval = await predictionContract.methods.intervalSeconds().call();
    return interval;
}

export const getMinBetAmount = async () => {
    const result = await predictionContract.methods.minBetAmount().call();
    return result;
}

//will be updated depending on frontend requirements
export const getPredictionData = async () => {
    const data = {};
    data.targetToken = await getPredictionTargetToken();
    data.bettingToken = await getPredictionBettingTokenName();
    data.roundDuration = await getPredictionIntervalSeconds();
    data.minBet = await getMinBetAmount();
    return data;
}

export const getCurrentRoundNo = async () => {
    const result = await predictionContract.methods.currentEpoch().call();
    return result;
}

export const getRoundData = async (roundNo) => {
    const result = await predictionContract.methods.rounds(roundNo).call();
    const decimal = oracleAddresses[oracleAddress].decimal;
    result.lockPrice = (new BigNumber(result.lockPrice).dividedBy(BIG_TEN.pow(decimal))).toString();
    result.closePrice = (new BigNumber(result.closePrice).dividedBy(BIG_TEN.pow(decimal))).toString();
    result.totalAmount = (new BigNumber(result.totalAmount).dividedBy(BIG_TEN.pow(18))).toString();
    return result;
}

export const getPreviousRoundData = async () => {
    const roundNo = await getCurrentRoundNo();
    const result = await getRoundData(roundNo-1);
    return result;
}

export const getCurrentRoundStartTimestamp = async () => {
    const currentRoundNo = await getCurrentRoundNo(predictionContract);
    const roundData = await getRoundData(currentRoundNo);
    return roundData.startTimestamp;
}

export const getCurrentRoundLockTimestamp = async () => {
    const currentRoundNo = await getCurrentRoundNo(predictionContract);
    const roundData = await getRoundData(currentRoundNo);
    return roundData.lockTimestamp;
}

const getOracle = async (_predictionIndex = null) => {
    const pid = _predictionIndex ? _predictionIndex : predictionIndex;
    const oracle = await factoryContract.methods.getOracleInfo(pid).call();
    return oracle;
}

export const getCurrentPrice = async (_predictionIndex = null) => {
    const pid = _predictionIndex ? _predictionIndex : predictionIndex;
    const oracle = await getOracle(pid);

    const geckoAPIid = oracleAddresses[oracle].geckoAPIid;
    const geckoAPIcurrency = oracleAddresses[oracle].geckoAPIcurrency;
    const priceRes = await fetch(`https://api.coingecko.com/api/v3/simple/price?ids=${geckoAPIid}&vs_currencies=${geckoAPIcurrency}`);
    const priceData = await priceRes.json();
    return priceData[geckoAPIid][geckoAPIcurrency];
}

