export const isClaimable = async (userAddress, roundNo) => {
    const res = await predictionContract.methods.claimable(roundNo, userAddress).call();
    if(!res) return {status:false};
    const ledger = await predictionContract.methods.ledger(roundNo, userAddress).call();
    return {status:!ledger.claimed, amount:ledger.amount };
}

export const isRefundable = async (userAddress, roundNo) => {
    const res = await predictionContract.methods.refundable(roundNo, userAddress).call();
    if(!res) return false;
    const ledger = await predictionContract.methods.ledger(roundNo, userAddress).call();
    return !ledger.claimed;
}

export const getUserRounds = async (userAddress) => {
    const res = await predictionContract.methods.getUserRounds(userAddress, 0, 100).call();
    return res[0];
}

export const getUserHistory = async (predictionIndex, userAddress) => {
    const userRounds = await getUserRounds(userAddress);
    if(userRounds.length === 0) return null;

    const geckoAPIid = oracleAddresses[oracleAddress].geckoAPIid;
    const geckoAPIcurrency = oracleAddresses[oracleAddress].geckoAPIcurrency;
    const tokenName = oracleAddresses[oracleAddress].tokenName;


    const priceRes = await fetch(`https://api.coingecko.com/api/v3/simple/price?ids=${geckoAPIid}&vs_currencies=${geckoAPIcurrency}`);
    const priceData = await priceRes.json();
    const tokenUSD = priceData[geckoAPIid][geckoAPIcurrency];

    const result = {numRounds:0, won:0, lost:0, tokenName:tokenName, tokenUSDPrice: tokenUSD, netEarning:BigNumber(0), avgReturn:BigNumber(0), bestRound:0, avgBet:BigNumber(0), rounds:[]};
    let ledger;
    let roundData;
    for (const roundNo of userRounds) {
        ledger = await predictionContract.methods.ledger(roundNo, userAddress).call();
        roundData = await getRoundData(roundNo);
        if(roundData.oracleCalled){
            const priceDiff = (BigNumber(roundData.closePrice).minus(roundData.lockPrice)).dividedBy(BIG_TEN.pow(8));
            const roundObject = {};
            roundObject.roundNo = roundNo;
            roundObject.priceDiff = priceDiff.toString();
            roundObject.position = ledger.position;
            roundObject.amount = ledger.amount;
            roundObject.claimed = ledger.claimed;
            roundObject.won = (roundData.closePrice > roundData.lockPrice && ledger.position == 0) ||
                              (roundData.closePrice < roundData.lockPrice && ledger.position == 1);

            result.rounds.push(roundObject);

            if(roundObject.won){
                result.won++;
                if(ledger.amount > result.bestRound) result.bestRound = ledger.amount;
                result.netEarning = (result.netEarning).plus(ledger.amount);
            }
            else
            { 
                result.lost++;
                result.netEarning = (result.netEarning).minus(ledger.amount);
            }

            result.avgBet = result.avgBet.plus(ledger.amount);
            result.numRounds++;
        }
    }

    result.avgBet = BigNumber(result.avgBet).dividedBy(result.numRounds).dividedBy(BIG_TEN.pow(18)).toString();
    result.avgReturn = BigNumber(result.netEarning).dividedBy(result.numRounds).dividedBy(BIG_TEN.pow(18)).toString();
    result.netEarning = BigNumber(result.netEarning).dividedBy(BIG_TEN.pow(18)).toString();
    result.bestRound = BigNumber(result.bestRound).dividedBy(BIG_TEN.pow(18)).toString();

    return result;
}

