export const getVolume24h = async (_predictionIndex) => {
    const response = await fetch(
        `https://predictionsfi-default-rtdb.europe-west1.firebasedatabase.app/userStatistics/${_predictionIndex}/volume.json`
    );
    const jsonData = await response.json();
    const amount = new BigNumber(jsonData).dividedBy(BIG_TEN.pow(18));
    const volume = parseFloat(amount);

    const bettingTokenAddress = await getPredictionBettingTokenAddress(_predictionIndex);
    const tokenName = tokenContracts[bettingTokenAddress].symbol;
    const geckoAPIid = tokenContracts[bettingTokenAddress].geckoid;
    const priceRes = await fetch(`https://api.coingecko.com/api/v3/simple/price?ids=${geckoAPIid}&vs_currencies=usd`);
    const priceData = await priceRes.json();
    const tokenPrice = priceData[geckoAPIid].usd;

    const totalUSD = (new BigNumber(jsonData).multipliedBy(tokenPrice).dividedBy(BIG_TEN.pow(18))).toString();

    return {tokenName, volume, tokenPrice, totalUSD};
}

export const getPriceChangePercentage = async (_predictionIndex) => {
    const oracle = await getOracle(_predictionIndex);
    const geckoAPIid = oracleAddresses[oracle].geckoAPIid;

    const priceRes = await fetch(`https://api.coingecko.com/api/v3/coins/${geckoAPIid}/market_chart?vs_currency=usd&days=1&interval=daily`);
    const priceData = await priceRes.json();
    
    const currentPrice = priceData['prices'][0][1];
    const oldPrice = priceData['prices'][1][1];

    return (100 * (parseFloat(currentPrice)- parseFloat(oldPrice)) / parseFloat(oldPrice)).toFixed(2);
}

