export const getPriceFromOracle = async(_predictionIndex = null) => {
    const pid = _predictionIndex ? _predictionIndex : predictionIndex;
    const oracleAddress = await getOracle(pid);
    const oracleContract = new web3.eth.Contract(oracleABI, oracleAddress);
    const result = await oracleContract.methods.latestRoundData().call()    
    .catch((error) => {
        console.log(error.message);
        return 0;
    });
    const decimal = oracleAddresses[oracleAddress].decimal;
    const price = (new BigNumber(result.answer).dividedBy(BIG_TEN.pow(decimal))).toString();
    return price;
}
