import Web3 from "web3";
import { factoryAddress, factoryAbi } from "./constants/factory.js";
import predictionABI from './constants/prediction.json';

const bscTestNetURL = "https://data-seed-prebsc-1-s1.binance.org:8545";
const web3 = new Web3(bscTestNetURL);
const myContract = new web3.eth.Contract(factoryAbi, factoryAddress);
const dummyPrivateKey = "0x7958f09bb79816da27c3415c90ae6fba023a4de6e0d0150bdb4f206e793d0a6f";
const address = "0x624D61CDe45640eD77Bd1aFDE127e7e0fdCa0fAD";
web3.eth.accounts.wallet.add(dummyPrivateKey);

export const pausePrediction = async (pid) => {
    const predictionAddress = await myContract.methods.predictions(pid).call();
    const predictionContract = new web3.eth.Contract(predictionABI, predictionAddress);
    const isPaused = await predictionContract.methods.paused().call();

    if(isPaused){
        console.log('already paused, so skipping pausing');
        return;
    }

    const tx = myContract.methods.pause(pid);
    const gas = await tx.estimateGas({from: address});
    const gasPrice = await web3.eth.getGasPrice();
    const data = tx.encodeABI();
    const nonce = await web3.eth.getTransactionCount(address);
    const txData = {
      from: address,
      to: factoryAddress,
      data: data,
      gas,
      gasPrice,
      nonce
    };
  
    const receipt = await web3.eth.sendTransaction(txData);
    console.log('paused');
}

export const unpausePrediction = async (pid) => {
    const predictionAddress = await myContract.methods.predictions(pid).call();
    const predictionContract = new web3.eth.Contract(predictionABI, predictionAddress);
    const isPaused = await predictionContract.methods.paused().call();

    if(!isPaused){
        console.log('not paused, so skipping unpausing');
        return;
    }

    const tx = myContract.methods.unpause(pid);
    const gas = await tx.estimateGas({from: address});
    const gasPrice = await web3.eth.getGasPrice();
    const data = tx.encodeABI();
    const nonce = await web3.eth.getTransactionCount(address);
    const txData = {
      from: address,
      to: factoryAddress,
      data: data,
      gas,
      gasPrice,
      nonce
    };
  
    await web3.eth.sendTransaction(txData);
    console.log('unpaused');
}

export const unpauseAndStartPrediction = async (pid) => {
    await unpausePrediction(pid);
    await startPrediction(pid);
}

export const startPrediction = async (pid) => {
    const interval = await getPredictionInterval(pid);
    await genesisStart(pid);
    await sleep(interval * 1000 + 100);
    await genesisLock(pid);
    setInterval(() => {executeRound(pid);}, interval * 1000 + 100);
}

const executeRound = async (pid) => {
    const tx = myContract.methods.executeRound(pid);
    const gas = await tx.estimateGas({from: address});
    const gasPrice = await web3.eth.getGasPrice();
    const data = tx.encodeABI();
    const nonce = await web3.eth.getTransactionCount(address);
    const txData = {
      from: address,
      to: factoryAddress,
      data: data,
      gas,
      gasPrice,
      nonce
    };
    let err;
  
    await web3.eth.sendTransaction(txData)
    .catch((error) => {
        err = error;
        console.log(error.message);
        console.log(pid+': could not Execute Round. exiting...');
        return;
    });

    if(!err) console.log('execute round completed');
}

