const getPredictionInterval = async (pid) => {
    const predictionAddress = await myContract.methods.predictions(pid).call();
    const predictionContract = new web3.eth.Contract(predictionABI, predictionAddress);
    const interval = await predictionContract.methods.intervalSeconds().call();

    return interval;
}

function sleep(ms) {
    return new Promise((resolve) => {
      setTimeout(resolve, ms);
    });
} 