import Modal from 'react-modal';
import React from 'react';
import styles1 from "./BetAmount.module.css";
import classnames from "classnames";
import arrow from "../assets/group_141.png";
import { getUserBettingTokenBalance,betBear, betBull, getCurrentRoundNo, isApproved, approvePrediction ,getMinBetAmount} from "../utils/contractData.js";
import { useSelector,useDispatch } from 'react-redux';
import conncetcode from "./conncetioncode.js";
import icon1 from "../assets/image_16.png";






Modal.setAppElement('#root');


function BetAmount(props, state) {
  var round=useSelector(state=>state.num);
  const dispatch = useDispatch();
  const [amount, setAmount] = React.useState(0);
  var web3 = useSelector(state => state.web3);
  var approve = useSelector(state => state.approve);
  var currentCurrency = useSelector(state => state.currentCurrency);
  var str = "Bet";
  if (web3 == null) {
    str = "Connect";
  }
  else {
    if (approve == false)
      str = "Approve"
  }
  var userAddress = useSelector(state => state.userAddress);


  var css; var label = props.label; var css2;
  if (props.label == "Enter Up") {
    css = styles1.button3;
    css2 = styles1.upbg;
  }
  else {
    css = styles1.button4;
    css2 = styles1.downbg;
  }

  const [modalIsOpen, setIsOpen] = React.useState(false);

  function openModal(props, state) {
    setIsOpen(true);
  }

  function afterOpenModal() {

  }

  function closeModal() {
    setIsOpen(false);
  }
  return (
    <>

      <button className={classnames(css, "font_5", "font_23_s", "font_28_m")} onClick={openModal}>{label}</button>
      <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        className={styles1.modal}


      >
        <div className={styles1.parent}>
          <img src={arrow} className={styles1.arrow} onClick={closeModal}></img></div>

        <div className={styles1.flex}>
          <div className={styles1.col1}><div className={classnames(styles1.amount, "font_5", "font_23_s", "font_22_m")}>Amount</div></div>
          <div className={styles1.col2}><img src={icon1} className={styles1.icon1}></img></div>
        </div>
        <div className={styles1.align}><input type="number" className={classnames(styles1.input, "font_29", "font_23_s", "font_31_m")} onChange={(event) => { setAmount(event.currentTarget.value) }}></input></div>
        <div className={styles1.align}><button className={classnames(styles1.submit, css2, "font_29", "font_23_s", "font_31_m")} onClick={() => {
          if (str == "Connect") {
            conncetcode().then((ret)=>{
              if(ret.error==false)
              {
                
                isApproved(ret.web3,ret.acounts,currentCurrency).then((result)=>{
                  if(result==true)
                  {
                    dispatch({ type: "approveChange",approve:true});
              
                  }
              
                })
                dispatch({ type: "web3Change",web3:ret.web3,userAddress:ret.acounts});
                
              }
              else{
                console.log(ret.errorMessage);
              }
                       });
           }
          else if (str == "Approve") {
            
            approvePrediction(web3,userAddress,currentCurrency).then((result)=>{
if(result==true)
{
  dispatch({ type: "approveChange",approve:true});
}
            })

           }
          else if (str == "Bet") {
           
            if (props.label == "Enter Up") {
              getMinBetAmount().then((minBet)=>{
                if(web3.utils.toWei(amount,"ether")>minBet) 
                {
                  getUserBettingTokenBalance(web3, userAddress,currentCurrency).then((balance)=>{
                    if(web3.utils.toWei(amount,"ether")<balance)
                    {
                      console.log(round)
                        betBull(web3, userAddress, round,web3.utils.toWei(amount,"ether")).then((res) => {
                           console.log(res); 
                           if(res==true) 
                           dispatch({ type: "betChange"}); 
                           closeModal(); 
                          });
                      }
                      else{
                      
                      console.log("you have not enough balance "+balance)
                      console.log("your be amount is "+web3.utils.toWei(amount,"ether"))
                    }
                  });
                
              }
                else
                {console.log("amount is lower than min Bet")}
              })


            }
            else {
              
                getMinBetAmount().then((minBet)=>{
                 
                  if(web3.utils.toWei(amount,"ether")>minBet) 
                  {
                    console.log(round)
                    getUserBettingTokenBalance(web3, userAddress,currentCurrency).then((balance)=>{
                      if(web3.utils.toWei(amount,"ether")<balance)
                      {
                      betBear(web3, userAddress, round,web3.utils.toWei(amount,"ether")).then((res) => { 
                        console.log(res);
                        if(res==true) 
                           dispatch({ type: "betChange"});  
                        closeModal(); 
                      });

                      }
                      else{
                        console.log("you have not enough balance")
                      }
                    });
                  
                }
                  else
                  {console.log("amount is lower than min Bet")}
                })
               
              

            }
          }


        }}>{str}</button></div>
      </Modal>
    </>
  );
}
export default BetAmount;
