import styles from "./Transactions.module.css";
import classnames from "classnames";
import icon1 from "../assets/image_16.png";
import icon2 from "../assets/image_14.png";
import icon3 from "../assets/image_15.png";
import icon4 from "../assets/image_17.png";
import icon5 from "../assets/image_18.png";
import icon6 from "../assets/image_19.png";
import React from "react";
import { useSelector, useDispatch} from "react-redux";
import  {getPriceChartData,isClaimable,hasStopped,setPredictionIndex,getVolume24h,getPredictionCount,getTotalUsers,getPriceChangePercentage,getCurrentRoundLockTimestamp,getPredictionIntervalSeconds,subscribePredictionEvents,getCurrentRoundNo, claim} from "../utils/contractData.js";


function Transactions() {
  const [vol24,setVol24]=React.useState(["","","","","",""]);
  const [totalUsers,setTotalUsers]=React.useState(["","","","","",""]);
  const [trend,setTrend]=React.useState(["","","","","",""]);

  React.useEffect(() => {
    async function f(){
var count=await getPredictionCount();
var v=[...vol24];
var u=[...totalUsers];
for(var i=0;i<count;i++)
{
var statistic =await getVolume24h(i);
v[i]=statistic.volume;
}
setVol24(v);

var u=[...totalUsers];
for(var i=0;i<count;i++)
{
var statistic =await getTotalUsers(i);
u[i]=statistic;

}
setTotalUsers(u);

var t=[...trend];
for(var i=0;i<count;i++)
{
var statistic =await getPriceChangePercentage(i);
if(statistic>0)
t[i]="UP";
else
t[i]="Down";

}
setTrend(t);
    }
    f();
  },[]);

  
  const currenctCurrency = useSelector(state => state.currentCurrency);
  var currencies=["BNB","BTC","ETH","APPLE","GOOGLE","TESLA"];
  var flags=["false","false","false","false","false","false"];
  flags[currenctCurrency]="true";
  return (
    
      <table className={classnames(styles.table1,styles.table1_s)}>
        <thead>
          <tr>
            <th
              className={classnames(
                styles.th1,
                styles.total,
                styles.thcustom,
                "font_20","font_20_s","font_29_m"
              )}
            >
              Assets 
            </th>
            <th
              className={classnames(
                styles.th11,
                styles.total,
                styles.thcustom,
                "font_20","font_20_s","font_29_m"
              )}
            ></th>
            <th
              className={classnames(
                styles.th2,
                styles.total,
                styles.thcustom,
                "font_20","font_20_s","font_29_m"
              )}
            >
              Select
            </th>
            <th
              className={classnames(
                styles.th3,styles.th3_s,
                styles.total,
                styles.thcustom,
                "font_20","font_29_m"
              )}
            >
              Users
            </th>
            <th
              className={classnames(
                styles.th4,styles.th4_s,
                styles.total,
                styles.thcustom,
                "font_20","font_29_m"
              )}
            >
              Volume 24h
            </th>
            <th
              className={classnames(
                styles.th5,styles.th5_s,
                styles.total,
                styles.thcustom,
                "font_20","font_29_m"
              )}
            >
              Trend 24h
            </th>
            <th
              className={classnames(
                styles.th6,styles.th6_s,
                styles.total,
                styles.thcustom,
                "font_20","font_29_m"
              )}
            ></th>
          </tr>
        </thead>
        <tbody>

        <TableRow asset="BNB" selected={flags[0]} users={totalUsers[0]} volume={vol24[0]} trend={trend[0]} index="0"/>
        <TableRow asset="BTC" selected={flags[1]} users={totalUsers[1]} volume={vol24[1]} trend={trend[1]}  index="1"/>
        <TableRow asset="ETH" selected={flags[2]} users={totalUsers[2]} volume={vol24[2]} trend={trend[2]}  index="2"/>
        <TableRow asset="APPLE" selected={flags[3]} users={totalUsers[3]} volume={vol24[3]} trend={trend[3]}  index="3"/>
        <TableRow asset="GOOGLE" selected={flags[4]} users={totalUsers[4]} volume={vol24[4]} trend={trend[4]}  index="4"/>
        <TableRow asset="TESLA" selected={flags[5]} users={totalUsers[5]} volume={vol24[5]} trend={trend[5]}  index="5"/>
        </tbody>
      </table>
 
  );
}

function TableRow(props,state)
{ 
  var userAddress=useSelector(state=>state.userAddress);
  //var round=useSelector(state=>state.num);
  //console.log("reward round num="+round);
    async function f(){

      dispatch({type:"currencyChange", currentCurrency:props.index});
      dispatch({type:"calculatedChange", calculated:true})
      await setPredictionIndex(props.index);
      dispatch({type:"betChange"})
      var stoped=await hasStopped();
      dispatch({type:"pausedChange", paused:stoped});
      const timestampLeft = await getCurrentRoundLockTimestamp();
      console.log(timestampLeft)
      var timer = Math.ceil(timestampLeft - (Date.now()/1000));  
      dispatch({type:"timeLeftChange", timeLeft:timer});
      var roundDuration = await getPredictionIntervalSeconds();
      console.log("duration is:"+roundDuration);
      console.log("timer is:"+timer);
      dispatch({type:"durationChange", duration:roundDuration}); 
      var currentRound=await getCurrentRoundNo();
        dispatch({type:"numChange", num:currentRound});

      const subscribeEventHandler = (eventName,roundNo) => { 
        switch(eventName) {
        case "PAUSED": 
        dispatch({type:"pausedChange", paused:true});
        console.log("PAUSED")
        break;
        case "UNPAUSED": 
        dispatch({type:"pausedChange", paused:false});
        console.log("UNPAUSED")
        break;
        case "STARTROUND":
          console.log("STARTROUND")
          dispatch({type:"pausedChange", paused:false});
          dispatch({type:"betChange"})
        var timer = roundDuration; 
        getCurrentRoundLockTimestamp().then((timestampLeft)=>{
          timer = Math.ceil(timestampLeft - (Date.now()/1000));
        dispatch({type:"timeLeftChange", timeLeft:timer});
        getCurrentRoundNo().then((currentRound)=>{
          dispatch({type:"numChange", num:currentRound});
        });
        
        })
        
        break;

        case "REWARDSCALCULATED":
             dispatch({type:"calculatedChange", calculated:true})

         /* if(userAddress!=null)
          {
          isClaimable(userAddress,roundNo).then((claim)=>{
 dispatch({type:"claim", claim:claim});
          });
         
        }*/
        console.log("REWARDSCALCULATED")
        dispatch({type:"claculated", claculated:true});
          break;
           
        default: }
        };
      subscribePredictionEvents(subscribeEventHandler);
    }

  const dispatch = useDispatch();
  var button;
  if(props.selected=="true")
  button=<button className={classnames(styles.button_selected, "font_21","font_21_s","font_30_m")} onClick={()=>{
    f(); 
 
  }}>Selected</button>
  else
  button=<button className={classnames(styles.button_select, "font_21","font_21_s","font_30_m")} onClick={
    ()=>{
      f();   
      
    }}>Select</button>

  var asseticon;
  if(props.asset=="BNB")
  asseticon=<img src={icon1} className={classnames(styles.icon1,styles.icon1_s)} ></img>
  else 
  if(props.asset=="BTC")
  asseticon=<img src={icon2} className={classnames(styles.icon1,styles.icon1_s)}></img>
  else if(props.asset=="ETH")
  asseticon=<img src={icon3} className={classnames(styles.icon1,styles.icon1_s)}></img>
  else if(props.asset=="APPLE")
  asseticon=<img src={icon4} className={classnames(styles.icon1,styles.icon1_s)}></img>
  else if(props.asset=="GOOGLE")
  asseticon=<img src={icon5} className={classnames(styles.icon1,styles.icon1_s)}></img>
  else
  asseticon=<img src={icon6} className={classnames(styles.icon1,styles.icon1_s)}></img>

  return(
  <tr>
            <td
              className={classnames(
                styles.td1,
                styles.total,
                styles.tdcustom,
                "font_20","font_20_s","font_29_m"
              )}
            >
              {asseticon}
            </td>
            <td
              className={classnames(
                styles.td11,
                styles.total,
                styles.tdcustom,
                "font_20","font_20_s","font_29_m"
              )}
            >
              <span className={styles.txt}>{props.asset}</span>
            </td>
            <td
              className={classnames(
                styles.td2,
                styles.total,
                styles.tdcustom,
                "font_21"
              )}
            >
{button}
              
            </td>
            <td
              className={classnames(
                styles.td3, styles.td3_s,
                styles.total,
                styles.tdcustom,
                "font_20","font_29_m"
              )}
            >
              <div className={styles.firstline}>{props.users}</div>
              <div className={classnames( styles.nextline)}>
                Total users
              </div>
            </td>
            <td
              className={classnames(
                styles.td4, styles.td4_s,
                styles.total,
                styles.tdcustom,
                "font_20","font_29_m"
              )}
            >
              <div className={styles.firstline}>{props.volume}</div>
              <div className={classnames( styles.nextline)}>
                Dollars
              </div>
            </td>
            <td
              className={classnames(
                styles.td5, styles.td5_s,
                styles.total,
                styles.tdcustom,
                "font_20","font_29_m"
              )}
            >
              <div className={styles.firstline}>{props.trend}</div>
              <div className={classnames( styles.nextline)}>
                &nbsp;
              </div>
            </td>
            <td className={classnames(styles.td6, styles.td6_s, styles.total,"font_6_m")}>
              .<br />.<br />.
            </td>
          </tr>);
}
export default Transactions;
