import React, { useRef, useState, useEffect  } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
import styles from "./Slider.module.css";


// Import Swiper styles
import "swiper/swiper.min.css";
import "swiper/components/pagination/pagination.min.css";

// import Swiper core and required modules
import SwiperCore, { Pagination ,Mousewheel} from "swiper/core";
import SliderPannel from "./SliderPannel";
import { propTypes } from "react-bootstrap/esm/Image";
import { useSelector} from "react-redux";

// install Swiper modules
SwiperCore.use([Pagination,Mousewheel]);

export default function Slider() {
  let [width, setWidth] = useState(document.documentElement.clientWidth);
  const num1=useSelector(state => state.num);

  useEffect(() => {
    const resizeListener = () => {
      // change width from the state object
      setWidth(document.documentElement.clientWidth)
    };
    // set resize listener
    window.addEventListener('resize', resizeListener);

    // clean up function
    return () => {
      // remove resize listener
      window.removeEventListener('resize', resizeListener);
    }
  }, []);

  /*
  var list = [];
  for (var i = 0; i < 10; i++)
    list.push(
      <SwiperSlide>
        {({ isActive, isPrev, isNext }) => <SliderPannel isactive={isActive} isnext={isNext} isprev={isPrev} activeindex={Swiper.activeIndex} slideindex={props.virtualIndex}/>}
      </SwiperSlide>
    );*/

    
    const params = {
      
      container: ".swiper-container",
    
      mousewheel: true,
     
    };
    var num=4;
    var space=20;
    var flag=true;
    if(width<390)
    {
      num=3;
      space=120;
      flag=false;

    }
    if(width>390&&width<=768)
    {
      num=3;
      space=20;
      flag=true;

    }

  return (
    <Swiper  {...params}
    
      slidesPerView={num}
      centeredSlides={flag}
      spaceBetween={space}
      initialSlide={2}  
      slideToClickedSlide={true}
      className={styles.swiper}
  >

      <SwiperSlide key='1'>
        {({ isActive, isPrev, isNext }) => <SliderPannel  isActive={isActive} status={"expired1"} />}
      </SwiperSlide>
      <SwiperSlide key='2'>
        {({ isActive, isPrev, isNext }) => <SliderPannel  isActive={isActive} status={"expired2"} />}
      </SwiperSlide>
      <SwiperSlide key='3'>
        {({ isActive, isPrev, isNext }) => <SliderPannel  isActive={isActive} status={"live"}  />}
      </SwiperSlide>
      <SwiperSlide key='4'>
        {({ isActive, isPrev, isNext }) => <SliderPannel  isActive={isActive} status={"next"} />}
      </SwiperSlide>
      <SwiperSlide key='5'>
        {({ isActive, isPrev, isNext }) => <SliderPannel isActive={isActive} status={"future1"} />}
      </SwiperSlide>
      <SwiperSlide key='6'>
        {({ isActive, isPrev, isNext }) => <SliderPannel isActive={isActive} status={"future2"} />}
      </SwiperSlide>
    </Swiper>
  );
}
