class CustomizedAxisTick extends PureComponent {
  render() {

    const { x, y, stroke, payload } = this.props;

    return (
      <g transform={`translate(${x},${y})`}>
         
        <text x={5} y={0} dy={5} textAnchor="end" fill="#B9B9B9" transform="rotate(0)"  className={styles.text} >
          {Math.floor(payload.value/10)}
        </text>
        <text x={5} y={0} dy={15} textAnchor="end" fill="#B9B9B9" transform="rotate(0)"  className={styles.text}>
          {payload.value %10}
        </text>
        
      </g>
    );
  }
}
